import React from 'react';
import Person from './components/Person'
import AddPersonForm from './components/AddPersonForm';
import personService from './services/people'
import Notification from './components/Notification'
import people from './services/people';

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      people: [],
      newName: '',
      newNumber: '',
      notification: null
    }
  }

  componentDidMount() {
      personService
        .getAll()
        .then(people => {
            this.setState({ people })
        })
  }

  addPerson = (event) => {
      event.preventDefault()

      var found = false 
      for(var i = 0; i < this.state.people.length; i++) {
          if (this.state.people[i].name === this.state.newName) found = true
      }

      if (found === true) {
          alert('Nimi on jo luettelossa')
      } else {
          const personObject = {
          name: this.state.newName,
          number: this.state.newNumber
        }
        
        personService
            .create(personObject)
            .then(response => {
                this.setState({
                    people: this.state.people.concat(response.data),
                    newName: '',
                    newNumber: '',
                    notification: `Lisättiin henkilö ${personObject.name}`
                })
                setTimeout(() => {
                    this.setState({notification: null})
                }, 5000)
            })

        console.log('lisätään henkilö')
    }
  }

  removePerson = (id) => {
    /*  if(window.confirm("Poistetaanko henkilö?")) {
        console.log('poisto')
        personService
          .remove(id)
      }
      */
    return () => {
        if(window.confirm("Poistetaanko henkilö?")) {
            console.log('poisto')
            personService.remove(id)
        }
    }
  }


  handleNameChange = (event) => {
    this.setState({ newName: event.target.value })
  }

  handleNumberChange = (event) => {
      this.setState({ newNumber: event.target.value })
    }

  render() {
    return (
      <div>
        <h2>Puhelinluettelo</h2>
        <Notification message={this.state.notification} />
        <form onSubmit={this.addPerson}>
            <AddPersonForm
                nameChange={this.handleNameChange}
                numberChange={this.handleNumberChange} 
                state={this.state}
            />
        </form>
        <h2>Numerot</h2>
        <ul>
            {this.state.people.map(person =>  
                <Person 
                    key={person.name} 
                    person={person}
                    removePerson={this.removePerson(person.id)} 
                />
            )}
        </ul>
      </div>
    )
  }
}

export default App