import React from 'react'


const AddPersonForm = ({ nameChange, numberChange, state }) => {
    return (
        <div>
          <div>
            nimi: 
            <input 
                value={state.newName} 
                onChange={nameChange}
            />
          </div>
          <div>
            numero:
            <input
                value={state.newNumber}
                onChange={numberChange} 
            />
          </div>
          <div>
            <button type="submit">lisää</button>
          </div>
        </div>
    )
}

export default AddPersonForm